.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
  }
  
  .navbar-col {
    
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .navbar-col-links a {
    margin: 0 10px;
    text-decoration: none;
    color: #0000ff;
    font-weight: bold;
  }
  
  .navbar-col-image img {
    width: 8.5em;
    height: auto;
  }
  
  .navbar-col-contact button {
    background-color: blue;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .prodotti-dropdown {
    position: relative;
  }
  
  .popover {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: white;
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .popover a {
    display: block;
    margin-bottom: 5px;
    color: black;
    text-decoration: none;
  }
  
  .popover a:last-child {
    margin-bottom: 0;
  }
  
  /* Responsive per tablet */
  @media (max-width: 768px) {
    .navbar-col-links, .navbar-col-contact {
      flex-direction: column;
    }
  
    .navbar-col-links a, .navbar-col-contact button {
      margin-bottom: 10px;
    }
  }
  
  /* Responsive per smartphone */
  @media (max-width: 480px) {
    .navbar {
      flex-direction: column;
    }
  
    .navbar-col {
      margin-bottom: 15px;
    }
  }
  