.custom-component {
    text-align: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  .allCustomContainer {
    background-position: center;
    background-image: url('../../Assets/background2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
  }
  .customButton {
    background: "linear-gradient(to right, #D3D3D3, #A9A9A9)";
    color: blue;
    border: none;
    font-weight: bold;
    margin: 1em auto 5em auto;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  .image-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-top: 4em;
  }
  .shrink-image img {
  width: 15em;
  }
  .image-item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .image-item img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
  }
  
  @media (max-width: 768px) {
    .image-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 480px) {
    .image-grid {
      grid-template-columns: 1fr;
    }
  }
  