body::-webkit-scrollbar {
    width: 0;
    /* Nasconde la barra di scorrimento su browser Webkit come Chrome e Safari */
  }
  
  body {
    -ms-overflow-style: none;
    /* Nasconde la barra di scorrimento su browser basati su IE e Edge */
  }
.header-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    overflow: hidden;
  }
  .background-container {
    background-image: url('../../Assets/background2.jpg');
    height: 100vh;
    width: 100%;
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .centered-text {
    z-index: 2;
    color: #1259a7;
    font-size: 2rem;
    text-align: center;
    padding: 20px;
    width: 15em;
    font-family: 'Times New Roman', Times, serif;
    animation: slideUp 1.8s ease forwards;
}
  
  .images-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .image-top {
    width: 50%;
    max-width: 300px;
    margin: 10px;
    opacity: 0;
    animation-duration: 1s;
    animation:slideFromRight 1.8s forwards;
  }
  .image-bottom {
    width: 100%;
    max-width: 300px;
    margin: 10px;
    opacity: 0;
    animation-duration: 1s;
    animation:slideFromLeft 1.8s forwards;
  }
  .image-top {
    animation-name: slideFromRight;
  }
  
  .image-bottom {
    animation-name: slideFromLeft;
  }
  .centered-text {
   animation-name: slideUp;
  }
  .contactButton {
    font-family: ui-sans-serif;
    background-color: #1259a7;
    border-radius: 5px;
    color:whitesmoke;
    position: absolute;
    top: 30px;
    right: 30px;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
  }
  @keyframes slideFromRight {
    0% {
      transform: translateX(100%);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }
  
  @keyframes slideFromLeft {
    0% {
      transform: translateX(-100%);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }
  @keyframes slideUp {
    from {
      transform: translateY(100px); /* Inizia da 100px sotto la posizione iniziale */
      opacity: 0; /* Parte da completamente trasparente */
    }
    to {
      transform: translateY(0);
      opacity: 1; /* Diventa completamente opaco */
    }
  }
  @media (max-width: 768px) {
    .centered-text {
      font-size: 1.5rem;
      width: 10em;
    }
  }
  
  @media (max-width: 480px) {
    .background-container {
      background-position: top;
    }
  
    .images-container {
      flex-direction: column;
      justify-content: space-around;
    }
  
    .image-top, .image-bottom {
      
    }
  }
  /* 2nd section */
  .text2ndSection {
    font-size: 20px;
    width: 60%;
    margin-inline: auto;
}
  /* 3trd section */
 .trdSection{
    width: 100%;
    background-image: url('../../Assets/background2.jpg');
    background-size: cover;
    background-position: center;

 }
  .card-container {
    display: flex;
    justify-content: center; /* Allinea le card orizzontalmente */
    overflow: hidden; /* Rimuove la barra di scorrimento orizzontale */
  }
  
  .card {
    width: 20%; /* Imposta una larghezza fissa in modo che ci siano sempre 5 card sulla stessa riga */
    margin-right: 20px; /* Aggiunge uno spazio tra le card */
    background-color: #fff; /* Imposta lo sfondo bianco per le card */
  }
  
  /* Stili delle card, puoi personalizzarli ulteriormente */
  .card {
    border: 1px solid #ccc;
    border-radius: 8px;
   width: 10%;
    margin-bottom: 15%;
  }
  
  .card img {
    width: 20%;
    height: auto;
  }
  
  /* Stili responsivi per schermi più piccoli */
  @media (max-width: 550px) {
    .card-container {
      flex-wrap: wrap; /* Permette il wrapping delle card su schermi più piccoli */
      justify-content: center; /* Allinea le card centralmente orizzontalmente */
    }
    .trdSection{
        width: 100%;
        display: flex;
        justify-content: center;
     }
    .card {
      width: calc(50% - 20px); /* Riduce la larghezza delle card su schermi più piccoli in modo che ce ne siano 2 per riga */
      margin-right: 20px;
      margin-bottom: 20px; /* Aggiunge spazio tra le card su schermi più piccoli */
    }
  }
  .service-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 auto;
    max-width: 1000px; /* Puoi regolare questa dimensione a seconda delle tue esigenze */
}
.services-section {
    width: auto;
  text-align: center;
